@import url(https://fonts.googleapis.com/css2?family=Lobster+Two:ital,wght@0,400;0,700;1,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.center {
    margin-left: -200px;
    margin-right: -200px;
}

.cover-konten {
  height: 60vh !important;
  align-items: center;
  position: relative;
}

.cover-konten .row {
  margin-right: 0;
  margin-left: 0;
}

.konten-image {
  /* max-width: 100%; */
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 25%;
  overflow: hidden;
  /* border: red 1px solid; */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover; /* Resize the background image to cover the entire container */
}

.header-image {
  width: 100%;
  position: absolute;
  /* bottom: 0; */
}

.sound {
  margin-bottom: 20px;
  z-index: 99;
}
.love {
  z-index: 99;
}
.liked {
  margin-top: 5px;
  font-size: 12px;
  margin-bottom: 0;
  color: white;
}

.side-menu {
  text-align: center;
  position: absolute;
  right: 10px;
  bottom: 20vh;
  z-index: 99;
}

.nama-konten {
  font-size: 40px;
}

.konten.row {
  margin-left: 0;
  margin-right: 0;
  text-align: center;
  color: #228b22;
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
  font-family: "Lobster Two", cursive;
}

.fakta {
  text-align: center;
  color: #228b22;
  font-family: "Lobster Two", cursive;
}

.fakta .card {
  color: black;
  width: 200px;
  /* height: 150px; */
  background-color: white;
  border-radius: 20px;
  margin-bottom: 20px;
  box-shadow: 7px 6px 5px 2px rgba(0, 0, 0, 0.5);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.tempatisi {
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;

  /* border: blue 1px solid; */
  padding-top: 3%;
  padding-bottom: 20%;
}

.isi {
  margin-top: 20px;
  margin-right: auto;
  margin-left: auto;
  width: 350px;
  height: 400px;
  background-color: white;
  color: black;
  border-radius: 20px;
  box-shadow: 7px 6px 5px 2px rgba(0, 0, 0, 0.5);
  font-family: "Lobster Two", cursive;
  font-size: 18px;
}

.ornament1 {
  position: absolute;
  width: 40%;
  left: -10%;
  bottom: 2%;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  -webkit-animation-name: aniorna1;
          animation-name: aniorna1;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes aniorna1 {
  25% {
    left: -30%;
  }
  50% {
    left: -10%;
  }
  75% {
    left: -30%;
  }
  100% {
    left: -10%;
  }
}

@keyframes aniorna1 {
  25% {
    left: -30%;
  }
  50% {
    left: -10%;
  }
  75% {
    left: -30%;
  }
  100% {
    left: -10%;
  }
}

.ornament2 {
  position: absolute;
  width: 40%;
  right: -5%;
  bottom: 1%;
  -webkit-animation-name: aniorna2;
          animation-name: aniorna2;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
  overflow-x: hidden;
  overflow-y: hidden;
}
@-webkit-keyframes aniorna2 {
  25% {
    bottom: 9%;
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  50% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    bottom: 1%;
  }
  75% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
    bottom: 9%;
  }
  100% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    bottom: 1%;
  }
}
@keyframes aniorna2 {
  25% {
    bottom: 9%;
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
  }
  50% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    bottom: 1%;
  }
  75% {
    -webkit-transform: rotate(-20deg);
            transform: rotate(-20deg);
    bottom: 9%;
  }
  100% {
    -webkit-transform: rotate(20deg);
            transform: rotate(20deg);
    bottom: 1%;
  }
}

.ornament3 {
  position: absolute;
  width: 30%;
  top: 3%;
  right: -10%;
  -webkit-animation-name: aniorna3;
          animation-name: aniorna3;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  /* transform: rotate(20deg); */
  overflow-x: hidden;
  overflow-y: hidden;
}

@-webkit-keyframes aniorna3 {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes aniorna3 {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.isi p {
  padding-top: 20px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 20px;
}

.card h4 {
  font-size: 20px !important;
}
.slick-prev, .slick-next {
    z-index: 99;
}

.slick-next {
    right: -19px;
}

.slick-prev::before, .slick-next::before {
    font-size: 30px;
    color: rgb(63, 63, 63);
}

@media (max-width: 768px) {
    .slick-prev {
        left: -15px;
    }
    
    .slick-next {
        right: -9px;
    }
}
html {
  scroll-behavior: smooth;
}
